/* General container */
.aboutUs{
    display: grid;
    grid-template-columns: 1fr 1fr 2fr;
    grid-template-rows: 1fr;
    height: 100%;
    width: 100%;
    padding: 5%;
    column-gap: 2%;
}

/* Text area */
.textContainer{
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    text-align: left;
    font-family: 'Outfit', 'sans-serif';
    font-size: large;
}

.yellowDivisionLine{
    height: 1%;
    width: 30%;
    background-color:  rgb(241, 199, 68);
    margin: 5% 5% 5% 0;
}

.bottomBox{
    position: absolute;
    bottom: 0;
    display: flex;
    border-bottom: rgb(50, 48, 49) solid 20px;
    align-items: center;
    justify-content: center;
    line-height: 0.59;
}

.years{
    color: rgb(241, 199, 68);
    font-size: 23vh;
    font-weight: 900;
}

.experience{
    font-size: xx-large;
    line-height: 1;
}

/* Building images */
.building1{
    background-image: url("../../media/images/building1.webp");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 100%;
    width: 100%;
}

.building2{
    background-image: url("../../media/images/building2.webp");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 100%;
    width: 100%;
}